/* Import Tailwind CSS */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom classes for the blue-green theme */
@layer components {
  /* Background gradients */
  .bg-blue-gradient {
    @apply bg-gradient-to-br from-blue-dark to-teal-dark;
  }

  .bg-header-gradient {
    @apply bg-gradient-to-r from-blue-primary to-teal-primary border-b border-blue-600;
  }

  /* Card styling */
  .card {
    @apply bg-blue-primary bg-opacity-30 p-6 rounded-lg shadow-lg border border-blue-700;
  }

  .card-title {
    @apply text-xl font-semibold mb-4 text-blue-accent;
  }
}